.nav {
    width: 100%;
    height: 69px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: rgb(36, 36, 36);
    top: 0;
    position: sticky;
    background-color: #FAF8FB;
    z-index: 2;
    box-shadow: 0 1px 2px rgba(0,0,0,0.15);
}

.scrolled {
    box-shadow: 0px 10px 28px 0px rgba(0,0,0,0.07);
}

.logo {
    margin-left: 1vw;
    padding: none;
    width: 145px;
    height: 34px;
    transition: all .2s ease-in-out;
}

.logo:hover {
    transform: scale(1.1);
}

.search-bar {
    margin-left: -120px;
}

.user-info-wrapper {
    display: flex;
    margin-right: 1vw;
    align-items: center;
    /* border: solid 1px red; */
}

.nav-button {
    font-size: 17px;
    text-decoration: none;
    color: rgb(36, 36, 36);
    transition-duration: 0.4s;
}

.nav-button:hover {
    color:#C1272D;
    cursor: pointer;
}

.logout-button {
    margin-right: 10px;
    background-color: transparent;
    text-decoration: none;
    font-size: 17px;
    font-weight: 100;
    border: none;
    cursor: pointer;
}